.header {
  background: rgb(33,56,100);
  background: -moz-linear-gradient(0deg, rgba(33,56,100,1) 0%, rgba(100,139,170,1) 100%);
  background: -webkit-linear-gradient(0deg, rgba(33,56,100,1) 0%, rgba(100,139,170,1) 100%);
  background: linear-gradient(0deg, rgba(33,56,100,1) 0%, rgba(100,139,170,1) 100%);
  filter: progid:DXImageTransform.Microsoft.gradient(startColorstr="#213864",endColorstr="#648baa",GradientType=1);
}

.header_contenedor{
  display: grid;
  margin: 0px;
  max-width: 1000px;
  max-height: 100px;
  grid-template-columns: 25% 20% 20% 20% 15%;
  grid-template-areas: "logo" "menu" "configuracion"
}

.header_contenedor > * {
    padding: 1rem;
}

.header_contenedor {
  grid-template-areas: 
  "logo menu menu menu configuracion";
  grid-template-columns: 5, 1fr;
  justify-content: center;
}

.logo {
  grid-area: logo;
  cursor: pointer;
}

.img_logo {
  max-width:100%;
  height:auto;
}


.menu {
  display: flex;
  grid-area: menu;
  margin-top: auto;
  margin-bottom: auto;
  justify-content: space-around;
}

.configuracion {
  grid-area: configuracion;
  margin: auto;
}

.btnSalashdr {
  background:linear-gradient(to bottom, #648bb3 5%, #305c83 100%);
  background-color:#648bb3;
  display:inline-block;
  cursor:pointer;
  color:#ffffff;
  font-family:Arial;
  font-size:17px;
  padding:10px 10px;
  text-decoration:none;
  border: none;
}
.btnSalashdr:hover {
  background:linear-gradient(to bottom, #305c83 5%, #648bb3 100%);
  background-color:#305c83;
}
.btnSalashdr:active {
  position:relative;
  top:1px;
}

.presionado {
  background:linear-gradient(to bottom, #cedeed 5%, #8cadcc 100%);
  background-color:#cedeed;
  display:inline-block;
  cursor:pointer;
  color:#25547e;
  font-family:Arial;
  font-size:17px;
  font-weight:bold;
  padding:10px 10px;
  text-decoration:none;
  border: none;  
}



