/*    481px — 768px: iPads, Tablets.*/
/* Third breakpoint */
@media screen and (max-width: 768px) {
  .img_logo {
    width:60%;
  }
  .presionado {
    font-size:10px;
  }
  .btnSalashdr {
    font-size:10px;
  }
  .header_contenedor{
    max-height: 70px;
  }
}

/* 769px — 1024px: Small screens, laptops. */
/* Second breakpoint */
@media screen and (min-width: 769px) and (max-width: 1024px) {
  .presionado {
    font-size:13px;
  }
  .btnSalashdr {
    font-size:13px;
  }
  .header_contenedor{
    max-height: 90px;
  }
}

